<template>
  <div>
    <template>
      <v-hover
        v-slot="{ hover }"
        open-delay="100"
      >
        <v-card
          :loading="loading"
          class="mx-auto my-12 noticias__card"
          width="250"
          max-height="450"
          :elevation="hover ? 5 : 2"
        >
          <div>
            <template slot="progress">
              <v-progress-linear
                color="deep-purple"
                height="10"
                indeterminate
              />
            </template>

            <cld-image
              :public-id="`terapiacl/blog/${value.Imagen}`"
              :version="value.VersionImagen"
              secure="true"
              style="width: 100%"
              :alt="value.Titulo"
            >
              <cld-transformation quality="auto" />
            </cld-image>
            <v-card-title
              class="card_title_noticias"
              style="word-break: normal;"
              v-html="
                value.Titulo.length > 58
                  ? value.Titulo.substring(0, 58) + '...'
                  : value.Titulo
              "
            />

            <v-card-text
              class="card_text_noticias"
              style="height: 130px; word-break: normal;"
            >
              <div
                v-html="
                  value.Texto.length > 140
                    ? value.Texto.substring(0, 140) + '...'
                    : value.Texto
                "
              />
            </v-card-text>
          </div>

          <div>
            <v-divider
              class="mx-4"
              style="padding: 0px; margin: 0px !important; margin-bottom: 8px;"
            />
            <v-card-text class="card_btn_noticias">
              <router-link :to="`/noticias-psicologia/noticia/${value.Id}`">
                Leer más
              </router-link>
            </v-card-text>
          </div>
        </v-card>
      </v-hover>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'CardNoticiasHome',

    props: {
      value: {
        Titulo: {
          type: Text,
          default: '',
        },
        Texto: {
          type: Text,
          default: '',
        },
        Imagen: {
          type: Text,
          default: '',
        },
        Id: {
          type: Number,
        },
        type: Object,
        default: null,
      },
    },

    data: () => ({
      loading: false,
      selection: 1,
    }),

    methods: {
      reserve () {
        this.loading = true

        setTimeout(() => (this.loading = false), 2000)
      },
    },
  }
</script>

<style scoped>
/* .noticias__card .v-btn {
  color: #fff !important;
} */
.noticias__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  height: 100%;
}

.noticias__card .v-img {
  padding: 10px !important;
}

.card_title_noticias {
  font-size: 15px !important;
  color: #595959 !important;
  padding: 5px 5px 5px 5px !important;
  line-height: 25px;
}

.card_text_noticias {
  font-size: 14px !important;
  color: #595959 !important;
  padding: 5px 5px 5px 5px !important;
  text-align: justify;
}

.card_btn_noticias {
  padding: 10px !important;
}

.noticias__card a {
  text-decoration: none;
  font-weight: bold;
  color: #36ba7f !important;
}
</style>
